<template>
<div>
    <el-row>
        <el-col :span="10" :push="7" class="user-info">
            <el-card>
                <template #header>
                    <span>基本信息</span>
                    <div class="btns">
                        <div v-show="!indexData">
                            <el-button type="text" @click="infoReadd=!infoReadd">信息补全</el-button>
                        </div>
                        <div v-show="indexData">
                            <el-button type="text" @click="updateshow.upDate=!updateshow.upDate;loginData()">编辑信息</el-button>
                            <el-button type="text" @click="updateshow.upDatePass=!updateshow.upDatePass;loginData()">修改密码</el-button>
                            <el-button type="text" @click="updateshow.upDateRole=!updateshow.upDateRole;loginData()">角色审核</el-button>
                        </div>
                    </div>
                </template>
                <el-row class="info-body" v-show="indexData">
                    <el-row>
                        <el-col :span="4">账号</el-col>
                        <el-col :span="20" v-text="userData.id"></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="4">昵称</el-col>
                        <el-col :span="20" v-text="userData.userid"></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="4">姓名</el-col>
                        <el-col :span="20" v-text="userData.name"></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="4">电话</el-col>
                        <el-col :span="20" v-text="userData.tel"></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="4">邮箱</el-col>
                        <el-col :span="20" v-text="userData.email"></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="4">拥有角色</el-col>
                        <el-col :span="20" v-html="statefilter(userData.hadRole)"></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="4">身份证号</el-col>
                        <el-col :span="20" v-text="userData.idnumber"></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="4">注册时间</el-col>
                        <el-col :span="20" v-text="utils.date.formatter(userData.regtime)"></el-col>
                    </el-row>
                </el-row>
                <el-row class="info-body" v-show="!indexData">
                    <el-alert type="error" :closable="false" title="基本信息不全" description="请补全基本信息" show-icon></el-alert>
                </el-row>
            </el-card>
        </el-col>
    </el-row>
    <el-dialog :visible.sync="infoReadd" width="600px" title="基本信息补全" :close-on-click-modal="false" @close="utils.formreset($refs['infoReaddForm'])">
        <el-form label-position="right" label-width="80px" ref="infoReaddForm" :model="userAdd" :rules="rules">
            <el-form-item label="角色" prop="role">
                <el-select v-model="userAdd.role" clearable>
                    <el-option v-for="item in role.alldatas" :key="item.id" :label="item.name" :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="邮箱" prop="email">
                <el-input placeholder="请输入您的个人邮箱" v-model="userAdd.email" type="email"></el-input>
            </el-form-item>
            <el-form-item label="姓名" prop="name">
                <el-input placeholder="请输入您的真实姓名" v-model="userAdd.name"></el-input>
            </el-form-item>
            <el-form-item label="身份证号" prop="idnumber">
                <el-input placeholder="请输入您的身份证号" v-model="userAdd.idnumber"></el-input>
            </el-form-item>
        </el-form>
        <template #footer>
            <el-button type="primary" @click.native="submitForm('infoReaddForm')">提交</el-button>
            <el-button type="warning" @click.native="utils.formreset($refs['infoReaddForm'])">重置</el-button>
        </template>
    </el-dialog>
    <el-dialog :visible.sync="updateshow.upDate" width="600px" title="基本信息修改" :close-on-click-modal="false" @close="utils.formreset($refs['updateInfoForm'])">
        <el-form label-position="right" label-width="80px" ref="updateInfoForm" :model="updateData" :rules="rules">
            <el-form-item label="昵称" prop="userid">
                <el-input placeholder="请输入新的昵称" v-model="updateData.userid" type="text"></el-input>
            </el-form-item>
            <el-form-item label="姓名" prop="name">
                <el-input placeholder="请输入新的姓名" v-model="updateData.name"></el-input>
            </el-form-item>
            <el-form-item label="邮箱" prop="email">
                <el-input placeholder="请输入新的邮箱" v-model="updateData.email" type="email"></el-input>
            </el-form-item>
        </el-form>
        <template #footer>
            <el-button type="primary" @click.native="UpdateData('updateInfoForm','updateDate')">提交</el-button>
            <el-button type="warning" @click.native="utils.formreset($refs['updateInfoForm'])">重置</el-button>
        </template>
    </el-dialog>
    <el-dialog :visible.sync="updateshow.upDatePass" width="600px" title="密码修改" :close-on-click-modal="false" @click="utils.formreset($refs['updatePwdForm'])">
        <el-form label-position="right" label-width="80px" ref="updatePwdForm" :model="updateData" :rules="rules">
            <el-form-item label="旧密码" prop="oldPass">
                <input-password placeholder="请输入旧密码" v-model="updateData.oldPass"></input-password>
            </el-form-item>
            <el-form-item label="新密码" prop="newpass">
                <input-password placeholder="请输入新密码" v-model="updateData.newpass"></input-password>
            </el-form-item>
            <el-form-item label="确认密码" prop="yPass">
                <input-password placeholder="请确认新密码" v-model="updateData.yPass" @input.native="inputres"></input-password>
            </el-form-item>
        </el-form>
        <template #footer>
            <el-button type="primary" @click.native="UpdateData('updatePwdForm','updatePwd')">提交</el-button>
            <el-button type="warning" @click.native="utils.formreset($refs['updatePwdForm'])">重置</el-button>
        </template>
    </el-dialog>
    <multiple-selection-form :visible.sync="updateshow.upDateRole" :title="multipleinit.title" :url="multipleinit.url" :width="multipleinit.width" :datatitle="multipleinit.datatitle"></multiple-selection-form>
</div>
</template>

<script>
export default {
    components: {
        inputPassword:()=>import('@/components/inputPassword.vue'),
        multipleSelectionForm:()=>import('@/components/multipleSelectionForm.vue')
    },
    data() {
        let vm = this;
        return {
            infoReadd: false, //信息补全弹窗显示状态
            indexData: false, //默认为false
            userAdd: {
                role: "",
                idnumber: "",
                name: "",
                email: ""
            },
            updateshow: {
                upDate: false,
                upDatePass: false,
                upDateRole: false
            },
            multipleinit: {
                width: "600px",
                title: "用户角色选择",
                url: "/allRole.action",
                updateurl: "/updataUserRole.action",
                datatitle: [{
                    prop: "id",
                    minwidth: '20%',
                    label: '角色id'
                }, {
                    prop: "name",
                    minwidth: '20%',
                    label: '角色名'
                }, {
                    prop: "detail",
                    minwidth: '45%',
                    label: '角色描述'
                }],
            },
            userData: {},
            updateData: {},
            role: {
                alldatas: [],
                haddatas: []
            },
            rules: {
                role: [{
                    trigger: 'blur',
                    required: true,
                    message: '必须选择角色'
                }],
                idnumber: [{
                    trigger: 'blur',
                    required: true,
                    message: '身份证号不能为空'
                }, {
                    trigger: 'blur',
                    validator: vm.$validateApi.idnumber
                }],
                userid: [{
                    trigger: 'blur',
                    required: true,
                    message: '新的昵称不能为空'
                }, {
                    trigger: 'blur',
                    name: '昵称',
                    len: 12,
                    validator: vm.$validateApi.cnLength
                }],
                email: [{
                    trigger: 'blur',
                    required: true,
                    message: '邮箱不能为空'
                }, {
                    trigger: 'blur',
                    type: 'email',
                    message: '邮箱格式不正确'
                }],
                name: [{
                    trigger: 'blur',
                    required: true,
                    message: '姓名不能为空'
                }, {
                    trigger: 'blur',
                    validator: vm.$validateApi.name
                }],
                oldPass: [{
                    trigger: 'blur',
                    required: true,
                    message: '旧密码不能为空'
                }],
                newpass: [{
                    trigger: 'blur',
                    required: true,
                    message: '新密码不能为空'
                }, {
                    validator: vm.$validateApi.password,
                    trigger: 'blur'
                }],
                yPass: [{
                    value: '',
                    validator: vm.$validateApi.validateRepass,
                    trigger: 'blur'
                }]
            }
        }
    },
    mounted: function () {
        let vm = this;
        vm.initData();
    },
    methods: {
        //验证密码绑定
        inputres() {
            let vm = this;
            vm.rules.yPass[0].value = vm.updateData.newpass;
        },
        //当前用户已拥有角色格式化显示
        statefilter(data) {
            let roleArr = [];
            let filtermap = new Map([
                [0, (name) => `${name}<span class='el-tag el-tag--warning'>未审核</span>`],
                [1, (name) => `${name}<span class='el-tag el-tag--success'>已审核</span>`],
                [2, (name) => `${name}<span class='el-tag el-tag--error'>冻结</span>`],
            ]);
            if (data) {
                data.forEach(function (role) {
                    roleArr.push(filtermap.get(Number(role.role_state))(role.rolename))
                })
            }
            return roleArr.join("  ");
        },
        //数据提交事件
        submitForm(ref) {
            let vm = this;
            let userAdd = vm.userAdd;
            vm.$refs[ref].validate(bool => {
                if (bool) {
                    vm.$axios
                        .post("/addData.action", vm.utils.data.formatter(userAdd))//
                        .then(success => {
                            let bool = bool;
                            vm.$message({
                                type: bool ? 'success' : 'error',
                                message: bool ? '补全成功' : success.msg,
                                duration: 1000
                            })
                        })
                        .catch(error => {
                            console.log(error)
                        });
                }
            })
        },
        //更新修改数据初始数据
        loginData() {
            this.updateData = this.utils.object.copy(this.userData);
        },
        //判断数据是否完整
        initData() {
            let vm = this;
            vm.$axios
                .post("/getUserDate.action")
                .then(function (success) {
                    if (success.bool) {
                        let data = success.data;
                        if (data.indexData == false) {
                            vm.indexData = false;
                        } else if (data.indexData == true) {
                            vm.userData = success.data;
                            vm.indexData = true;
                        }
                    }
                    vm.loginRoleSelectData() ;
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        //获取角色
        loginRoleSelectData() {
            let vm = this;
            vm.$axios
                .post("/allRole.action")
                .then(function (success) {
                    let res = success.data;
                    if (success.bool) {
                        vm.role.alldatas = res.allRole;
                        vm.role.haddatas = res.hadRole;
                    }
                })
                .catch(function (err) {
                    console.log(err)
                });
        },
        //修改信息
        UpdateData(ref, title) {
            let vm = this;
            let data = {
                title: title,
                data: vm.updateData
            };
            vm.$axios
                .post("/UserDate.action",vm.utils.data.formatter(data))
                .then(function (success) {
                    let res = success.data;
                    let bool = success.bool;
                    if (bool) {
                        vm.loginData();
                        for (let key in vm.updateshow) {
                            vm.updateshow[key] = false;
                        }
                        vm.$message({
                            message: '修改成功',
                            type: 'success'
                        })
                        vm.initData();
                    }
                })
                .catch(function (err) {
                    console.log(err);
                });
        }
    }
}
</script>

<style scoped>
.user-info {
    min-width: 600px;
    font-size: 16px;
}

.user-info .btns {
    float: right;
}

.user-info .info-body .el-row {
    padding: 5px 0;
}
</style>
